'use client';

import Link from 'next/link';
import { useAuth } from '@/lib/contexts/AuthContext';
import { useState } from 'react';

export default function Navbar() {
    const { user, logout } = useAuth();
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

    return (
        <nav className="bg-white shadow-md sticky top-0 z-50">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex justify-between items-center h-16">
                    {/* Logo */}
                    <Link href="/" className="flex items-center space-x-2">
                        <div className="w-8 h-8 bg-gradient-to-br from-purple-600 to-blue-600 rounded-lg flex items-center justify-center">
                            <span className="text-white font-bold text-xl">P</span>
                        </div>
                        <span className="text-xl font-bold bg-gradient-to-r from-purple-600 to-blue-600 bg-clip-text text-transparent">
                            PeakTech
                        </span>
                    </Link>

                    {/* Desktop Navigation */}
                    <div className="hidden md:flex items-center space-x-8">
                        <Link href="/shop" className="text-gray-700 hover:text-purple-600 transition">
                            Shop
                        </Link>
                        {user ? (
                            <>
                                <Link href="/orders" className="text-gray-700 hover:text-purple-600 transition">
                                    Orders
                                </Link>
                                <Link href="/cart" className="text-gray-700 hover:text-purple-600 transition">
                                    Cart
                                </Link>
                                {user.role === 'admin' && (
                                    <Link href="/admin" className="text-gray-700 hover:text-purple-600 transition">
                                        Admin
                                    </Link>
                                )}
                                <button
                                    onClick={() => logout()}
                                    className="px-4 py-2 bg-gray-100 text-gray-700 rounded-lg hover:bg-gray-200 transition"
                                >
                                    Logout
                                </button>
                            </>
                        ) : (
                            <>
                                <Link
                                    href="/login"
                                    className="px-4 py-2 text-purple-600 hover:text-purple-700 transition"
                                >
                                    Login
                                </Link>
                                <Link
                                    href="/register"
                                    className="px-4 py-2 bg-gradient-to-r from-purple-600 to-blue-600 text-white rounded-lg hover:from-purple-700 hover:to-blue-700 transition"
                                >
                                    Sign Up
                                </Link>
                            </>
                        )}
                    </div>

                    {/* Mobile Menu Button */}
                    <button
                        className="md:hidden p-2"
                        onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
                    >
                        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                        </svg>
                    </button>
                </div>

                {/* Mobile Menu */}
                {mobileMenuOpen && (
                    <div className="md:hidden py-4 border-t">
                        <Link href="/shop" className="block py-2 text-gray-700 hover:text-purple-600">
                            Shop
                        </Link>
                        {user ? (
                            <>
                                <Link href="/orders" className="block py-2 text-gray-700 hover:text-purple-600">
                                    Orders
                                </Link>
                                <Link href="/cart" className="block py-2 text-gray-700 hover:text-purple-600">
                                    Cart
                                </Link>
                                {user.role === 'admin' && (
                                    <Link href="/admin" className="block py-2 text-gray-700 hover:text-purple-600">
                                        Admin
                                    </Link>
                                )}
                                <button
                                    onClick={() => logout()}
                                    className="mt-2 w-full text-left py-2 text-gray-700 hover:text-purple-600"
                                >
                                    Logout
                                </button>
                            </>
                        ) : (
                            <>
                                <Link href="/login" className="block py-2 text-gray-700 hover:text-purple-600">
                                    Login
                                </Link>
                                <Link href="/register" className="block py-2 text-gray-700 hover:text-purple-600">
                                    Sign Up
                                </Link>
                            </>
                        )}
                    </div>
                )}
            </div>
        </nav>
    );
}
